main
{
    margin: 0rem 10rem;
    .Heading
    {
        text-align: center;

        .Heading-title
        {
            font-size: xx-large;
            hyphens: none;
            overflow-wrap:break-word;
            .nonbreak
            {
                display: inline-block;
            }
        }

        .Heading-author
        {
            font-size: large;
            .authorspace 
            {
                margin-right: 3rem;
            }
        }

        .Heading-affiliation
        {
            margin: 1rem 0rem;
        }
        .Heading-submissions
        {
            font-size: large;
        }
        .Heading-images
        {
            float: left;            
            max-width: 100%;
            width: 100%; /* IE対応 */
            height: auto;
        }

        .Img-systemoverview
        {

        };
    }
    
    h2
    {
        color: #A05359;
    }
    ul
    {
        line-height: 2;
    }
    hr
    {
        height: 1px;
        margin: 1rem 5rem;
        background-color: #f1e1e1;
        border: none;
    }
    div
    {
        margin: 1rem 5rem;
        hyphens: auto;
        line-height: 1.5;
    }


    .Abstract, .Acknowledgement
    {
        text-align: justify;
    }


}

footer
{
    font-size: small;
    text-align: center;
    background-color: #fff8ff;
    padding: 0.8rem;
    margin: 2rem 0 0 0;
    width: 100%;
}
